import React from "react";
import {Jumbotron} from "./migration";
import {Container} from "react-bootstrap";
import {useScrollPosition} from "../../hooks/useScrollPosition";
import {useTranslation} from 'react-i18next';
import {BsFillStarFill} from 'react-icons/bs';

const Skills = React.forwardRef(({heading, skills}, ref) => {
    const {t} = useTranslation();
    const skillsTabRef = React.useRef(null);
    const [isScrolled, setIsScrolled] = React.useState(false);

    //const navbarDimensions = useResizeObserver(navbarMenuRef);
    useScrollPosition(
        ({prevPos, currPos}) => {
            if (!isScrolled && currPos.y - 400 < 0) setIsScrolled(true);
        },
        [],
        skillsTabRef
    );

    return (
        <Jumbotron ref={skillsTabRef} fluid className="bg-white m-0" id="skills">
            <Container className="p-5 ">
                <h2 ref={skillsTabRef} className="display-4 pb-5 text-center">
                    {t(heading)}
                </h2>
                <div className="d-flex flex-sm-wrap">
                    <div>
                        <h3>Web development</h3>
                        <div className="skillsContainer">
                            {skills.filter(i => i.group === "web").map((skill, index) => (
                                <div key={index} className="skill">
                                    <div className="skillHidden shadow-lg">
                                        <h6>{skill.name}</h6>
                                        <img src={skill.img} alt={skill.name}/>
                                        <div className="stars">
                                            {new Array(skill.stars).fill(0).map((i, j) =>
                                                <BsFillStarFill key={j}/>)
                                            }
                                        </div>
                                    </div>

                                    <img src={skill.img} alt="skill"/>
                                </div>
                            ))}
                        </div>
                    </div>
                    <div>
                        <h3>Databases</h3>
                        <div className="skillsContainer">
                            {skills.filter(i => i.group === "bbdd").map((skill, index) => (
                                <div key={index} className="skill">
                                    <div className="skillHidden shadow-lg">
                                        <h6>{skill.name}</h6>
                                        <img src={skill.img} alt={skill.name}/>
                                        <div className="stars">
                                            {new Array(skill.stars).fill(0).map((i, j) =>
                                                <BsFillStarFill key={j}/>)
                                            }
                                        </div>
                                    </div>

                                    <img src={skill.img} alt="skill"/>
                                </div>
                            ))}
                        </div>
                    </div>
                    <div>
                        <h3>Game development</h3>
                        <div className="skillsContainer">
                            {skills.filter(i => i.group === "gamedev").map((skill, index) => (
                                <div key={index} className="skill">
                                    <div className="skillHidden shadow-lg">
                                        <h6>{skill.name}</h6>
                                        <img src={skill.img} alt={skill.name}/>
                                        <div className="stars">
                                            {new Array(skill.stars).fill(0).map((i, j) =>
                                                <BsFillStarFill key={j}/>)
                                            }
                                        </div>
                                    </div>

                                    <img src={skill.img} alt="skill"/>
                                </div>
                            ))}
                        </div>
                    </div>
                    <div>
                        <h3>Otros</h3>
                        <div className="skillsContainer">
                            {skills.filter(i => i.group === "other").map((skill, index) => (
                                <div key={index} className="skill">
                                    <div className="skillHidden shadow-lg">
                                        <h6>{skill.name}</h6>
                                        <img src={skill.img} alt={skill.name}/>
                                        <div className="stars">
                                            {new Array(skill.stars).fill(0).map((i, j) =>
                                                <BsFillStarFill key={j}/>)
                                            }
                                        </div>
                                    </div>

                                    <img src={skill.img} alt="skill"/>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </Container>
        </Jumbotron>
    );
});

export default Skills;
